#page-room{

    header {
        padding: 24px;
        border-bottom: 1px solid #e2e2e2;
    
        .content {
            max-width: 1128px;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
            align-items: center;

            > img{
                max-height: 45px;
            }

            > div {
                display: flex;
                

                button {
                    height: 40px;
                    margin-left: 16px;
                    
                }
            }
        }
    }

    main {

        max-width: 800px;
        margin: 0 auto;

        .room-title {
            margin: 32px 0 24px;
            display: flex;
            align-items: center;

            h1{
                font-family: 'Poppins', sans-serif;
                font-size: 24px;
                color: #29292e;
            }

            

            span {
                margin-left: 16px;
                background-color: #e559f9;
                border-radius: 9999px;
                padding: 8px 16px;
                color: #fff;
                font-weight: 500;
                font-size: 14px;
            }
        }
        
        form{
            textarea{
                width: 100%;
                border: 0;
                padding: 16px;
                border-radius: 16px;
                background-color: #fefefe;
                box-shadow: 0 2px 12px rgba(0, 0, 0, 0.4);
                resize: vertical;
                min-height: 130px;
            }

            .form-footer{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 16px;

                .user-info{
                    display: flex;
                    align-items: center;
    
                    img{
                        width: 32px;
                        height: 32px;
                        border-radius: 50%;
                    }
    
                    span{
                        margin-left: 8px;
                        color: #29292e;
                        font-weight: 500;
                        font-size: 14px;
                    }
                }

                > span{
                    font-size: 14px;
                    color: #737388;
                    font-weight: 500;

                    button {
                        background-color: transparent;
                        border: 0;
                        color: #835AFD;
                        text-decoration: underline;
                        font-size: 14px;
                        font-weight: 500;
                        cursor: pointer;
                    }
                }    
            }
        }

        .question-list {
            margin-top: 32px;
        }
    }
}